import React from 'react';
import Stack from '@mui/material/Stack';

const CircuitElement = ({ active, text, color }) => {
  const elementStyle = {
    width: '30px',
    height: '30px',
    borderRadius: '50%',  // Make it a circle by setting border-radius to 50%
    border: '1px solid #000',
    backgroundColor: active ? color : 'gray',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  };

  const textStyle = {
    width: '70px',
    height: '30px',
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
  };
  return (

    <Stack direction="row" spacing={2}>
    
    <div style={elementStyle}>
      {/* You can add more content inside the element */}
    </div>
    <div style={textStyle}>
      {text}
    </div>
   </Stack>


  );
};

export default CircuitElement;