    this.networkSeries.links.template.strokeOpacity = 0;

    this.networkSeries.nodes.template.adapter.add("fill", (fill, target) => {
      if (target.dataItem) {
        // Check the name of the node and set a custom color based on the name
        const tag = target.dataItem.dataContext.tag;
        return this.getNodeFillColor(tag);
      }
      // Return a default fill if no match is found
      return fill;
    });

    this.networkSeries.nodes.template.adapter.add('stroke', (stroke, target) => {
      if (target.dataItem) {
        const tag = target.dataItem.dataContext.tag;
        return this.getNodeFillColor(tag);
      }
      return stroke;
    });

