  componentWillUnmount() {
    if (this.chart) {
      this.chart.dispose();
    }
  }

  getNodeFillColor(tag) {
    // Define the fill color based on the node's name
    switch (tag) {
      case 'AI':
        return am4core.color('#6AB2E3');
      case 'Computer Architecture':
        return am4core.color('#77B87D');
      case 'Big Data':
        return am4core.color('#FFE45B');
      case 'Network & Security':
        return am4core.color('#B58CDC');
      case 'Software Engineering':
        return am4core.color('#AACCB4');
      case 'HCI':
        return am4core.color('#FF6B6B');
      case 'Computer Graphics':
        return am4core.color('#FF9BBB');
      case 'Algorithms':
        return am4core.color('#FFA389');
      default:
        return am4core.color('#999999'); // Default color
    }
  }


