import React, { Component } from 'react';
import professorsData from './professors.json';

class ProfessorSearch extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchName: '',
      keywords: [],
      numColumns: 3, // Change this to the desired number of columns
    };
  }

  handleSearch = (searchName) => {
    const professor = professorsData.find((prof) => prof.name === searchName);

    if (professor) {
      this.setState({ keywords: professor.keywords });
    } else {
      this.setState({ keywords: [] });
    }
  };

  renderKeywords() {
    const { keywords, numColumns } = this.state;
    const numRows = Math.ceil(keywords.length / numColumns);

    const keywordLists = [];
    for (let i = 0; i < numColumns; i++) {
      const columnKeywords = keywords.slice(i * numRows, (i + 1) * numRows);

      keywordLists.push(
        <ul key={i}>
          {columnKeywords.map((keyword, index) => (
            <li key={index}>{keyword}</li>
          ))}
        </ul>
      );
    }

    return keywordLists;
  }

  render() {
    const { keywords } = this.state;
  
    return (
      <div>
        <div>
          {keywords.length > 0 ? (
            <div>
              <h2>Professor's Keywords:</h2>
              <div className="columns-container">
                {this.renderKeywords()}
              </div>
            </div>
          ) : (
            <p>No keywords found for the professor.</p>
          )}
        </div>
      </div>
    );
  }
  
}

export default ProfessorSearch;
